/* Week5.css */

.week5-container1 {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 10px solid rgb(0, 0, 0);
    background-color: rgb(255, 166, 0);
    border-radius: 40px;
  }
  .week5-container2{
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 10px solid rgb(251, 0, 255);
    margin-top: 20px;
    background-color: cyan;
    border-radius: 40px;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    

  }
  
  .card {
    border: 10px solid #ccc;
    background-color:lightcoral ;
    border-radius: 5px;
    overflow: hidden;

    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  
  .card:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    transform: translateY(-30px) translateX(-30px);
  }
  
  .card-body {
    padding: 15px;
  }
  
  .card-title {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .card-text {
    font-size: 14px;
    margin-bottom: 5px;
  }

  .emp_img{
    margin-top: 10px;
    background-color: lightseagreen;
  }
  
